.noside{
    display: none;
}
@media all and (max-width:796px){
    .sidebar{
        height:100vh;
        min-width:230px;
        margin: auto;
        border-top:1px solid red;
        background-color: rgb(49, 38, 38);
        display:flex;
        flex-direction: column;
        padding: 60px 40px;
        position: fixed;
        margin-top: 10vh;
        color: white;
    }

    .sidebar a{
        text-decoration: none;
        color:white;
        padding:10px 0px;
        font-size: 25px;
        font-weight: 600;
    }

    .sidebar a:hover{
        background-color: white;
        color: crimson;
        border-radius: 5px;
    }

    .noside{
        display: none;
    }
}

@media all and (max-width:480px){
    .sidebar{
        min-width:200px;
        padding: 50px 30px;
        margin-top: 10vh;
    }

    .sidebar a{
        font-size: 20px;
    }
}