.resume{
    padding-top: 10vh;
    margin-bottom: 50px;
}

a button{
    padding:10px 20px;
    margin:30px;
    cursor: pointer;
    font-size: 20px;
    border-radius: 20px;
    border:none;
    background-color: blue;
    color:white
}

.pdf{
    width:100%;
    margin: auto;
    height:1150px;
    overflow: hidden;
}

@media all and (max-width:800px){
    .pdf{
        height:500px;
    }
}

@media all and (max-width:400px){
    .pdf{
        width:100%;
        margin: auto;
        height:450px;
    }
}