.NavBar{
    height:10vh;
    width:100%;
    background-color: rgb(49, 38, 38);
    display: flex;
    justify-content: space-between;
    position: fixed;
}

.show,.hide{
    display: none;
}

.name p{
    font-size: 35px;
    margin: 5px 2px 0px 10px;
    font-weight: 600;
    color:white;
}

.links{
    display: flex;
    margin: 18px 10px 0px 0px;
}

.links a{
    text-decoration: none;
    color:white;
    font-size: 25px;
    font-weight: 600;
    border-radius: 5px;
    margin-bottom: 5px;
    padding: 0px 10px;
}

.links a:hover{
    background-color: white;
    color:crimson;
}

@media all and (max-width:1024px){
    
    .links a{
        font-size: 20px;
        padding:0px 6px;
        margin-bottom: 13px;
    }
}

@media all and (max-width:796px){
    .hide{
        margin: 3vh;
        display: flex;
        flex-direction: column;
        gap:6px;
    }

    .hide div{
        width:40px;
        border-bottom: 5px solid white;
    }

    .show{
        margin: 2vh;
        display: flex;
        flex-direction: column;
        gap:6px;
    }

    .show div{
        width:40px;
        border-bottom: 5px solid white;
    }

    .show .one{
        transform: rotate(45deg);
        margin-top: 15px;
    }
    .show .two{
        display: none;
    }
    .show .three{
        transform: rotate(-45deg);
        margin-top: -11px;
    }

    .name p{
        font-size: 30px;
        margin: 12px 50px 0px 0px;
        font-weight: 600;
        color:white;
    }

    .links{
        display: none;
    }
}

