.home{
    padding-top: 10vh;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.con1{
    display: grid;
    grid-template-columns: 3fr 3fr;
}

.con1 p:first-child{
    padding:25% 0% 25% 10%;
    font-size: 35px;
    font-weight: 600;
    color: rgb(46, 44, 44);
}

.con1 p b{
    color: blue;
}

.home1{
    background-image: url("../Images/home.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}


@media all and (max-width:1024px){
    .con1{
        grid-template-columns: 3fr 3fr;
        gap:20px;
        width:100%;
        margin:auto;
    }
    .con1 p:first-child{
        padding:35% 0% 35% 7%;
        font-size: 25px;
    }
    .home1{
        margin-right: 20px;
    }
}

@media all and (max-width:796px){
    .con1{
        margin-top: 10%;
        grid-template-columns: 1fr;
        gap:3%;
    }
    .con1 p:first-child{
        width:450px;
        margin: auto;
        padding:5% 1% 5% 1%;
        font-size: 27px;
    }
    .home1{
        height:300px;
        margin-top: 0px;
    }
}

@media all and (max-width:480px){
    .con1 p:first-child{
        width:100%;
    }
}