.about{
    padding-bottom: 10vh;
    margin-top: 10vh;
    padding-top: 10vh;
    background-image: url("../Images/about.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.hh{
    color: rgb(176, 176, 196);
    margin-top: 0px;
}

.container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 3% 0%;
}

.container1 img{
    width:50%;
}

.type p{
    font-size: 140%;
    display: flex;
    flex-direction: column;
    justify-content:center;
    padding-right: 15%;
    color:white;
}

@media all and (max-width:1024px){
    .type p{
        font-size: 120%;
    }
}

@media all and (max-width:796px){
    .about{
        width:100%;
        margin-left: -5px;
    }
    .container{
        display: grid;
        grid-template-columns: 1fr;
        padding: 0% 0%;
    }
    .container img{
        width:50%;
        margin-left: 10px;
    }
    .type p{
        font-size: 140%;
        padding: 0% 10%;
    }
}

@media all and (max-width:480px){
    .about{
        width:102%;
    }
    .type p{
        font-size: 100%;
    }
}