.contact{
    padding-top: 10vh;
    height:600px;
    background-image: url("../Images/contact.svg");
    background-size: 40%;
    background-repeat: no-repeat;
    background-position: right top 20%;
}

.content{
    width: 50%;
    padding:5% 2%;
}

.content :last-child .cimg{
    transform: rotate(180deg);
}

.cont{display: flex; width: 300px; margin: auto; padding-bottom: 10px;}

.cimg{
    width:35px;
    height:35px;
    border-radius: 5px;
}

.anc{
    margin: 5px 0px 0px 10px;
    font-size: 20px;
    font-weight: bold;
}

.anc a{
    text-decoration: none;
    color: rgb(68, 68, 228);
}

@media all and (max-width:796px){
    .contact{
        background-position: center top;
        background-size: 70%;
    }
    .content{
        width:80%;
        margin: auto;
        margin-top:40% ;
        padding: 10px 10px;
    }
}

@media all and (max-width:480px){
    .contact{
        background-size: 100%;
    }
    .content{
        margin-top: 50%;
        
    }
}